<template>
  <view class="editor">
    <textarea :value="value" :placeholder="title" @blur="onTextarea" />
  </view>
</template>

<script>
export default {
  props: {
    value: {
      type: String,
      default: () => ""
    }
  },
  data() {
    return {
      title: "开始输入"
    };
  },
  methods: {
    onTextarea(e) {
      this.$emit("update:value", e.target.value);
    }
  },
  mounted() {
    this;
  }
};
</script>

<style lang="scss" scoped>
.editor {
  border: 1rpx solid black;
}
</style>